.ai_search_body {
  width : 100%;
  min-height: 85vh;
  background: linear-gradient(to bottom, #F7FFF9 60%, #FFFFFF 40%);
  font-size:50px;

  display: flex;
  justify-content: center;
  align-items: start;

  padding-top:50px;
}

.ai_search_group {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ai_search_input {
  width: 95%;
  height: 95%;
  font-size:18px;
  border:none;
  outline: none;
  background-color: transparent;
}

.ai_search_input_box {
  width: 600px;
  height: 60px;
  border-radius: 30px;
  border: 3px solid #CEF5D9;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ai_search_button{
  background : transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  /*background-color: #A0EBB5;*/
  border-radius: 50%;
  border:4px solid #CEF5D9;
  width: 65px;
  height: 65px;
  cursor : pointer;
}

